const perr = (id, info)=>
    be_lib.perr_ok({id, info}, true);

class Switch extends React.Component {
    toggle = ()=>{
        let {on_change} = this.props;
        if (on_change)
            this.props.on_change(!this.props.checked);
    };
    render(){
        let {checked} = this.props;
        let cls = 'switch'+(checked ? ' switch-checked' : '');
        return (
            <button type="button" role="switch" aria-checked={checked}
              className={cls} onClick={this.toggle}/>
        );
    }
}
Switch.defaultProps = {
    on_change: ()=>{},
};

function Label_checkbox(props){
    let {children, ...input_props} = props;
    return <label className='label-checkbox'>
      <input type='checkbox' {...input_props}/>
      <div className='content'>{children}</div>
    </label>;
}

const Link_button = props=>{
    const on_click = e=>{
        if (!props.href)
            e.preventDefault();
        if (props.on_click)
            props.on_click(e);
    };
    return <a className={classnames('link-button', props.cls)}
      style={props.style} href={props.href} onClick={on_click}
      target={props.new_tab ? '_blank' : undefined}>
      {props.children}
    </a>;
};

const Link_line = props=>
    <div className='link-line'>
      <Link_button href={props.href}>{props.children}</Link_button>
    </div>;

const Row = props=>
    <div className='row-sp1'>
      <div className={classnames('row-children', props.cls)}>
        {props.children}
      </div>
    </div>;

function Report_problem(){
    return (
        <div className='section report-problem'>
          <a className='title' href='about.html#report_issue'>
            <T>Report a problem</T>
          </a>
        </div>
    );
}

const date2display = d=>d ? date.strftime('%o %B %Y', date(d)) : '-';

const Mobile_cancel_notice = props=>{
    const store = membership.is_ios(props.membership) ? 'App Store' :
        'Google Play';
    const faq_url = props.hola_faq ? '/faq#premium-cancel' :
        membership.is_ios(props.membership) ?
        'https://support.apple.com/en-il/HT202039' :
        'https://support.google.com/googleplay/answer/7018481';
    return <p>
      Note: To cancel your subscription, you must use {store}.
      See <a href={faq_url}>the FAQ.</a>
    </p>;
};

class Cancel_modal extends PureComponent {
    componentDidMount(){
        this.setState({step: 'faq'});
        this.history = [];
    }
    on_change = e=>this.setState({[e.target.name]: e.target.value});
    render_faq(){
        const Faq = props=><a href={'https://hola.org/faq#'+props.id}>
          {props.children}</a>;
        let body = <div>
          <p><b>Common issues:</b></p>
          <ul>
            <li>
              <Faq id="premium-streamingservices">Netflix/Hulu issues</Faq>
            </li>
            <li><Faq id="premium-slowconn">Slow connection</Faq></li>
            <li>
              <Faq id="premium-appearfree">My account appears as free</Faq>
            </li>
          </ul>
          <p>Search the <Faq id="faq">Full FAQs</Faq></p>
          <p>Still need help? <a href="/premium_support">Contact us!</a></p>
        </div>;
        return {
            title: T('Technical issue?'),
            next_label: T('Still want to cancel'),
            body,
        };
    }
    render_reason(){
        let reason = this.state.reason || '';
        let body = <input className='hfill' name='reason'
            value={reason} onChange={this.on_change}
            placeholder={'Problems with a specific site? '
              +'Slow connection? Please let us know.'}/>;
        return {
            title: T('Before you go... tell us why?'),
            next_label: T('Continue'),
            next_disabled: reason.trim().length < 8,
            body,
        };
    }
    render_cancel(){
        let {cancel_error} = this.state;
        let body = <div>
          <p>
            The subscription allows you to enjoy uninterrupted secure browsing
            and unblocking of all your favorite sites, on all your devices.
          </p>
          {cancel_error && <Alert_message type='error'>
            {cancel_error}</Alert_message>}
        </div>;
        return {
            title: T('Are you sure you want to cancel your subscription?'),
            next_label: T('Keep using Hola VPN'),
            back_label: T('Cancel my subscription'),
            body,
        };
    }
    render_cancel_mobile(){
        return {
            next_label: T('Ok'),
            back_label: false,
            body: <Mobile_cancel_notice membership={this.props.membership}/>,
        };
    }
    cancel_subscr(){
        let _this = this;
        return etask(function*(){
            this.on('uncaught', ()=>{
                perr('cancel_subscription_unknown_error', {
                    user: zutil.get(_this.props.user, 'hola_uid'),
                    is_mobile: membership.is_mobile(_this.props.membership)});
                _this.setState({cancel_error: 'Unknown error'});
            });
            let data = {reason: _this.state.reason};
            let res = yield be_bg_ajax.ecall('hola_api_call',
                ['users/payment/cancel_subscription?no_redirect=1',
                {method: 'POST', text: true, data}]);
            if (res!='ok')
                throw new Error();
            be_premium.refresh_user({force_premium: true});
            _this.props.close();
        });
    }
    send_reason(){
        let _this = this;
        return etask(function*(){
            try {
                let data = {reason: _this.state.reason};
                let res = yield be_bg_ajax.ecall('hola_api_call',
                    ['users/payment/update_reason', {method: 'POST',
                    text: true, data}]);
                return res.trim()=='ok';
            } catch(e){
                return false;
            }
        });
    }
    perr_commit = ()=>{
        perr('cancel_subscription', {
            user: zutil.get(this.props.user, 'hola_uid'),
            is_mobile: membership.is_mobile(this.props.membership)});
    };
    on_back = ()=>{
        if (this.state.step == 'cancel')
        {
            this.perr_commit();
            return this.cancel_subscr();
        }
        let step = this.history.pop();
        if (!step)
            return this.props.close();
        this.setState({step});
    };
    on_next = ()=>{
        let _this = this;
        let {step} = this.state;
        this.history.push(step);
        return etask(function*(){
            let next_step;
            switch (step)
            {
            case 'faq': next_step = 'reason'; break;
            case 'reason':
                if (membership.is_mobile(_this.props.membership))
                {
                    _this.perr_commit();
                    next_step = (yield _this.send_reason()) ? 'cancel_mobile' :
                        'reason';
                }
                else
                    next_step = 'cancel';
                break;
            default:
                be_premium.refresh_user({force_premium: true});
                next_step = undefined;
                _this.props.close();
            }
            _this.setState({step: next_step});
        });
    };
    render(){
        let {step} = this.state;
        let res = this[`render_${step}`] && this[`render_${step}`]();
        if (!res)
            return null;
        const actions = [
            res.back_label!==false && <a key='back' className='btn-secondary'
              onClick={this.on_back}>
              {T(res.back_label || 'Go back')}
            </a>,
            <a key='next' className={classnames({disabled: res.next_disabled})}
              onClick={!res.next_disabled && this.on_next}>
              {res.next_label}
            </a>,
        ];
        return <Modal title={res.title} on_close={this.props.close}
          action={actions}>
          {res.body}
        </Modal> ;
    }
}

function Alert_message(props){
    let {duration} = props;
    if (duration && props.ts + duration < Date.now())
        return null;
    return <div className={classnames('alert-message', props.type)}>
      {props.children}
    </div>;
}

function create_alert_info(type, message, duration){
    return {type, message, duration, ts: Date.now()}; }

class Account_details extends PureComponent {
    state = {password: '', new_pass: ''};
    constructor(props){
        super(props);
        this.switch_show_password = this.switch_show.bind(this,
            'show_change_password');
        this.switch_show_manage = this.switch_show.bind(this,
            'show_manage_subscr');
        this.switch_show_cancel = this.switch_show.bind(this,
            'show_cancel_modal');
    }
    componentDidMount(){
        this.model_to_state(be_premium, 'membership');
        be_premium.refresh_user({force_premium: true});
    }
    on_change = e=>this.setState({[e.target.name]: e.target.value});
    switch_show = key=>this.setState({[key]: !this.state[key]});
    update_alert = (id, type, message, duration)=>{
        let {alerts = {}} = this.state;
        if (!type)
            return this.setState({alerts: {...alerts, [id]: undefined}});
        this.setState({alerts: {...alerts,
            [id]: create_alert_info(type, message, duration)}});
        if (duration)
            setTimeout(()=>this.forceUpdate(), duration);
    };
    logout_sessions = ()=>{
        let _this = this;
        etask(function*(){
            this.on('uncaught', ()=>{
                _this.update_alert('logout', 'error',
                    'Unsuccessful logout all sessions except me');
            });
            _this.update_alert('logout');
            let res = yield be_bg_ajax.ecall('hola_api_call',
                ['users/logout/others', {method: 'POST', text: true}]);
            if (res!='ok')
                throw new Error();
            _this.update_alert('logout', 'success',
                'Successful logout all sessions except me', 3000);
        });
    };
    change_password = ()=>{
        let _this = this;
        let {password, new_pass} = this.state;
        return etask(function*(){
            this.on('uncaught', ()=>{
                _this.update_alert('password', 'error',
                    'Current password is incorrect');
            });
            _this.update_alert('password');
            if (!password || !new_pass)
            {
                return _this.update_alert('password', 'error',
                    'Please fill all fields');
            }
            let data = {password, new_pass, service: true};
            let res = yield be_bg_ajax.ecall('hola_api_call',
                ['users/change_password', {method: 'POST', data, text: true}]);
            if (res!='ok')
                throw new Error();
            yield _this.logout_sessions();
            _this.setState({show_change_password: false});
            _this.update_alert('password', 'success',
                'Password successfully changed', 3000);
        });
    };
    avangate_update_cc = ()=>{
        let _this = this;
        return etask(function*(){
            this.on('uncaught', ()=>{
                _this.update_alert('avangate_update_cc', 'error',
                    'Unknown error');
            });
            _this.update_alert('avangate_update_cc');
            let url = yield be_bg_ajax.ecall('hola_api_call',
                ['users/payment/single_sign_on']);
            if (!zurl.is_valid_url(url))
                throw new Error();
            location.href = url.replace('?', 'payment_methods/?');
        });
    };
    render_alerts(keys){
        let {alerts = {}} = this.state;
        return Object.entries(alerts)
          .filter(([key, value])=>!!value && keys.includes(key))
          .map(([key, {message, ...props}])=>
            <Alert_message key={key} {...props}>
              {message}
            </Alert_message>);
    }
    render(){
        let {user, is_plus} = this.props;
        let {show_change_password, show_manage_subscr, show_cancel_modal} =
            this.state;
        let m = this.state.membership;
        let is_active = membership.is_active(m);
        const status = !membership.is_active(m) ? 'Stopped' :
            membership.is_in_trial(m) ? 'Free trial' : 'Valid';
        return <Section title={T('Account details:')}>
          <Label_line label={T('Account ID:')}>
            {user.displayName} ({user.hola_uid})
          </Label_line>
          <Label_line label={T('Membership:')}>
            {is_plus ? 'PLUS' : 'FREE'}
          </Label_line>
          <Label_line>
            <Row cls='hfill'>
              <Link_button on_click={this.switch_show_password}>
                {show_change_password ? 'Hide change password' :
                  'Change password'}
              </Link_button>
              <Link_button on_click={this.logout_sessions}>
                Logout all sessions except me
              </Link_button>
            </Row>
            {this.render_alerts(['logout', 'password'])}
            {show_change_password && <div>
              <Label_line label={T('Current password:')}>
                <input name='password' type='password'
                  onChange={this.on_change}/>
              </Label_line>
              <Label_line label={T('New password:')}>
                <input name='new_pass' type='password'
                  onChange={this.on_change}/>
              </Label_line>
              <Label_line label=' '>
                <Link_button cls='btn-primary' style={{marginRight: '20px'}}
                  on_click={this.change_password}>
                  Change password
                </Link_button>
                <Link_button href='https://hola.org/forgot_password'
                  new_tab={true}>
                  Forgot your password?
                </Link_button>
              </Label_line>
            </div>}
          </Label_line>
          {is_active && <Label_line label={T('Subscribed on:')}>
            {membership.is_mobile(m) ? 'Mobile' : 'Desktop'}
          </Label_line>}
          {is_active && <Label_line label={T('Subscription:')}>
            {membership.period_str(m)}
          </Label_line>}
          {is_active && m.cancelled && <Label_line>
            You cancelled your subscription. It will remain active
            until {date2display(m.end)}.
          </Label_line>}
          {is_active && <Label_line>
            <Row cls='center'>
              <Link_button on_click={this.switch_show_manage}>
                {show_manage_subscr ? T('Hide') : T('Manage')}
              </Link_button>
            </Row>
            {show_manage_subscr && <div>
              <Label_line label={T('Started on:')}>
                {membership.period_start_str(m)}
              </Label_line>
              <Label_line
                label={T(m.period=='1 D' ? 'Expires on:': 'Next renewal:')}>
                {membership.period_end_str(m)}
              </Label_line>
              <Label_line label={T('Payment method:')}>
                {membership.payment_label(m)}
                {m.gateway=='avangate' && <div>
                  <Link_button on_click={this.avangate_update_cc}>
                    Update Credit card
                  </Link_button>
                </div>}
                {this.render_alerts(['avangate_update_cc'])}
              </Label_line>
              <Label_line label={T('Subscription status:')}>
                {status}
                {membership.is_cancellable(m) && <div>
                  <Link_button on_click={this.switch_show_cancel}>
                    Cancel subscription
                  </Link_button>
                  {show_cancel_modal &&
                    <Cancel_modal close={this.switch_show_cancel}
                      user={user} membership={m}/>}
                </div>}
              </Label_line>
              {membership.is_cancellable_mobile(m) && <Label_line>
                <Mobile_cancel_notice membership={m}/>
              </Label_line>}
            </div>}
          </Label_line>}
          <Link_line href='https://hola.org/cp'>
            <T>My account</T>
          </Link_line>
        </Section>;
    }
}

function Rule_item(props){
    let {rule, href, cls, on_remove, on_switch, title} = props;
    return (
        <li key={rule.name} className={classnames('rule-item', cls)}>
          <div className={'icon-'+(props.icon||rule.mode||'unblock')}/>
          <div className="f32">
            <i className={'flag '+rule.country}></i>
          </div>
          {href && <a className="rule-name" href={href} target="_blank"
            rel="noopener noreferrer">
            {rule.name}
          </a>}
          {title && <div className="rule-name"><T>{title}</T></div>}
          {on_switch && <Switch checked={rule.enabled} on_change={on_switch}/>}
          {on_remove && <div className="icon-remove" title={T('Delete')}
            onClick={()=>on_remove(rule)}/>}
        </li>
    );
}

class Unblock extends PureComponent {
    componentDidMount(){
        this.model_to_state(be_rule, 'rules');
        this.model_to_state(be_vpn, ['protect_pc', 'protect_browser']);
        this.model_to_state(be_svc, 'vpn_country');
        this.load_mitm_rules();
    }
    load_mitm_rules(){
        let _this = this;
        etask(function*(){
            let rules = yield be_vpn.fcall('get_mitm_unblock_rules');
            _this.setState({mitm_rules: rules});
        });
    }
    remove_rule = rule=>{
        let _this = this;
        etask(function*(){
            if (rule.mitm)
            {
                yield be_vpn.fcall('mitm_set_ignore', [rule.url]);
                _this.load_mitm_rules();
                return;
            }
            let opt = assign({enabled: 0, del: 1}, _.pick(rule, 'sid', 'name',
                'type', 'md5', 'country'));
            yield be_rule.fcall('set_rule', [opt]);
            be_rule.trigger('fetch_rules');
        });
    };
    render_rule = rule=>{
        let href = 'http://'+(zurl.get_host(zurl.add_proto(rule.link||'')+'/')
            || rule.name);
        return <Rule_item rule={rule} href={href} on_remove={this.remove_rule}
            cls={{'rule-disabled': !rule.enabled}}/>;
    };
    switch_all_browser = enable=>{
        be_vpn.fcall('set_enabled_for_browser', [enable, {country: 'us'}]); };
    switch_protect_pc = enable=>{
        be_vpn.fcall('set_enabled_for_pc', [enable, {country: 'us'}]); };
    get_rules(){
        let {state} = this;
        let rules = [], all_browser;
        Object.values(zutil.get(state, 'rules.unblocker_rules', {}))
            .forEach(rule=>{
                if (be_vpn_util.is_all_browser(rule))
                    all_browser = rule;
                else
                    rules.push(rule);
            });
        (state.mitm_rules||[]).forEach(r=>rules.push({name: r.host, url: r.url,
            country: r.proxy_country, enabled: true, mitm: true}));
        let protect_pc = {mode: 'protect', country: state.vpn_country || 'us',
            enabled: !!state.vpn_country};
        all_browser = all_browser || {country: 'us', enabled: 0};
        return {rules, all_browser, protect_pc};
    }
    render(){
        let {rules, all_browser, protect_pc} = this.get_rules();
        let {country, is_plus} = this.props;
        let popular_url = 'https://hola.org/unblock/popular'+(country ?
            '/'+country.toLowerCase() : '')+'?utm_source=holaext_settings';
        return <Section title={T('Unblock')}>
          <ul>
            {is_plus && this.state.protect_browser &&
            <Rule_item rule={all_browser} title='Protect browser'
              on_switch={this.switch_all_browser} icon='protect'/>}
            {is_plus && this.state.protect_pc &&
            <Rule_item rule={protect_pc} title='Protect entire PC'
              on_switch={this.switch_protect_pc} icon='protect'/>}
            <li className="sub-section">
              <h3 className="title"><T>Sites</T></h3>
              {!!rules.length &&
              <ul>{rules.map(this.render_rule)}</ul>}
              <a className="popular-btn" target="_blank"
                rel="noopener noreferrer" href={popular_url}>
                <T>Unblock more sites...</T>
              </a>
            </li>
          </ul>
        </Section>;
    }
}

class Popups extends PureComponent {
    state = {rules: []};
    componentDidMount(){
        be_info.on('change:settings', this.update_rules);
        this.update_rules();
    }
    componentWillUnmount(){
        super.componentWillUnmount();
        be_info.off('change:settings', this.update_rules);
    }
    update_rules = ()=>{
        let _this = this;
        etask(function*(){
            let all_sites = false;
            let rules = yield be_info.ecall('get_dont_show_rules',
                ['default']);
            if (rules.includes('all'))
            {
                all_sites = true;
                rules = rules.filter(r=>r!='all');
            }
            _this.setState({rules, all_sites});
        });
    };
    on_change = enable=>{
        be_info.ecall('set_dont_show_again', [{root_url: 'all',
            period: 'default', type: 'default', src: 'settings',
            unset: !enable}]);
    };
    on_remove = ({name})=>{
        be_info.ecall('set_dont_show_again', [{root_url: name, type: 'default',
            unset: true}]);
    };
    render_rule = root_url=>{
        let href = 'http://'+root_url;
        return <Rule_item rule={{name: root_url}} href={href}
          on_remove={this.on_remove} cls='rule-item-popups'/>;
    };
    render(){
        let {rules, all_sites} = this.state;
        return <Section title={T('Popups')}>
          <ul>
            <li className="settings-item">
              <T>Don't show popup for any site</T>
              <Switch checked={all_sites} on_change={this.on_change}/>
            </li>
            <li className='sub-section'>
              <h3 className='title'><T>Sites</T></h3>
              {!!rules.length &&
              <ul>{rules.map(this.render_rule)}</ul>}
            </li>
          </ul>
        </Section>;
    }
}

class Peer extends React.Component {
    state = {checked: true};
    on_change = ()=>{
        let _this = this;
        etask(function*(){
          _this.setState({checked: false});
          yield etask.sleep(100);
          _this.setState({checked: true, modal: true});
        });
    };
    on_close_modal = ()=>this.setState({modal: false});
    render_modal(){
        let plus_url = be_vpn_util.plus_ref('p2p_settings');
        let get_plus = <a className='upgrade-btn' href={plus_url}
          target='_blank' rel="noopener noreferrer">
          <T>Upgrade to</T><i className='icon-plus'></i>
        </a>;
        return <Modal title={T('Upgrade to')} action={get_plus}
          on_close={this.on_close_modal}>
          Upgrade to PLUS to stop sharing idle resources
        </Modal>;
    }
    render(){
        let {is_plus} = this.props;
        return <Section title={T('Peer to peer')}>
          <ul>
            <li className="settings-item">
              {is_plus && <T>Never share idle resources</T>}
              {!is_plus && <T>Allow to be a peer and share resources</T>}
              {!is_plus && <Switch checked={this.state.checked}
                on_change={this.on_change}/>}
            </li>
          </ul>
          {this.state.modal && this.render_modal()}
        </Section>;
    }
}

class Legal extends PureComponent {
    componentDidMount(){
        this.model_to_state(window.be_bg_main, 'agree_ts');
    }
    show_privacy = e=>{
        e.preventDefault();
        this.setState({modal: true});
    };
    hide_privacy = ()=>this.setState({modal: false});
    on_change = ()=>{
                                                                   
        let agree_ts = this.state.agree_ts ? '' : Date.now();
        this.setState({agree_ts});
        window.be_bg_main.ecall('set_agree_ts', [agree_ts]);
    };
    render_modal(){
      return <Modal className='page-modal-legal' title={privacy.title}
        on_close={this.hide_privacy}>
        <privacy.Text/>
      </Modal>;
    }
    render(){
        return <Section title={T('Legal')} cls='section-legal'>
          <ul>
            <li className='settings-item'>
              <Label_checkbox onChange={this.on_change}
                checked={!!this.state.agree_ts}>
                I agree that Hola can use my personal information as
                described in the {' '}
                <a href='' onClick={this.show_privacy}
                  onMouseDown={e=>e.preventDefault()}>{privacy.title}</a>
              </Label_checkbox>
            </li>
          </ul>
          {this.state.modal && this.render_modal()}
        </Section>;
    }
}

class Settings extends PureComponent {
    componentDidMount(){
        perr('settings_show');
        this.model_to_state(be_premium, 'user');
        this.model_to_state(be_ext, 'is_premium', 'is_plus');
        this.model_to_state(be_info, 'country');
        this.model_to_state(be_svc, 'version');
        be_svc.fcall('update_info');
    }
    render(){
        let {state} = this;
        let user_info = {user: state.user, is_plus: state.is_plus};
        return (
            <Page_layout {...user_info} title={T('Settings')} cls='settings'>
              <Report_problem/>
              {state.user && <Account_details {...user_info}/>}
              <Unblock country={state.country} is_plus={state.is_plus}/>
              <Popups/>
              {!!state.version && <Peer is_plus={state.is_plus}/>}
              {!!conf.check_agree_ts && <Legal/>}
              <Legal_section>
                <Link_line href='about.html'>
                  <T>About</T>
                </Link_line>
              </Legal_section>
            </Page_layout>
        );
    }
}

const uninit = ()=>{
    B.backbone.client.stop('be_bg_main');
    B.backbone.client.stop('be_ext');
    B.backbone.client.stop('be_rule');
    B.backbone.client.stop('be_premium');
    B.backbone.client.stop('be_vpn');
    B.backbone.client.stop('be_svc');
    B.backbone.client.stop('be_info');
    B.backbone.client.stop('be_bg_ajax');
};

export const init = ()=>{
    $(window).on('unload', uninit);
    B.init({context: null});
    window.be_bg_main = B.backbone.client.start('be_bg_main');
    be_ext = B.backbone.client.start('be_ext');
    be_rule = B.backbone.client.start('be_rule');
    be_premium = B.backbone.client.start('be_premium');
    be_vpn = B.backbone.client.start('be_vpn');
    be_svc = B.backbone.client.start('be_svc');
    be_info = B.backbone.client.start('be_info');
    be_bg_ajax = B.backbone.client.start('be_bg_ajax');
    ReactDOM.render(<Settings/>, $('.react-root')[0]);
};
